import {
  motion,
  useAnimation,
  useViewportScroll,
  Variants,
} from "framer-motion";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { NavLink, useHistory, useRouteMatch } from "react-router-dom";
import styled from "styled-components";

const Nav = styled(motion.div)`
  background-color: rgba(255, 255, 255, 0.678);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  height: 60px;
  font-size: 15px;
  padding: 0 60px;
  z-index: 999;
`;
const Col = styled.div`
  display: flex;
  align-items: center;
`;
const Logo = styled(motion.svg)`
  margin-right: 50px;
  width: 95px;
  height: 25px;
  fill: #ff0000;
  path {
    stroke-width: 6px;
    stroke: #ff0000;
  }
`;
const Items = styled.ul`
  display: flex;
  align-items: center;
  list-style: none;
`;
const Item = styled(motion.li)`
  margin-right: 30px;
  transition: color 0.3s ease-in-out;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  &:hover {
    color: #7d2525;
  }
`;
const Circle = styled(motion.span)`
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 5px;
  width: 5px;
  border-radius: 5px;
  background-color: ${(prop) => prop.theme.red};
`;
const Search = styled.form`
  display: flex;
  align-items: center;
  color: #000000;
  svg {
    height: 25px;
  }
  margin-right: 15px;
`;
const logoVariants: Variants = {
  inActive: { fillOpacity: 1 },
  active: {
    fillOpacity: [0, 1, 0],
    transition: {
      repeat: Infinity,
    },
  },
};
const Input = styled(motion.input)`
  transform-origin: right center;
  height: 23px;
  width: 190px;
  font-size: 12px;
`;
interface IForm {
  keyword: string;
}

function Header() {
  const history = useHistory();
  const { register, handleSubmit } = useForm();
  const [searchOpen, onSearchOpen] = useState(false);
  const homeMatch = useRouteMatch("/");
  const tvMatch = useRouteMatch("/tv");
  const { scrollY } = useViewportScroll();
  const scrollAnimation = useAnimation();
  const scrollAnimationMenuText = useAnimation();
  const scrollAnimationSvg = useAnimation();

  const onClickSearch = () => {
    onSearchOpen((prev) => !prev);
    if (searchOpen) {
      scrollAnimationSvg.start({ x: 0 });
    } else {
      scrollAnimationSvg.start({ x: -212 });
    }
  };
  useEffect(() => {
    scrollY.onChange(() => {
      if (scrollY.get() > 100) {
        scrollAnimation.start({ backgroundColor: "rgba(0, 0, 0, 1)" });
        scrollAnimationMenuText.start({ color: "rgba(255, 255, 255, 1)" });
      } else {
        scrollAnimation.start({
          backgroundColor: "rgba(255, 255, 255, 0.678)",
        });
        scrollAnimationMenuText.start({ color: "rgba(0, 0, 0, 1)" });
      }
    });
  }, [scrollY]);
  useEffect(() => {
    scrollY.onChange(() => {
      if (scrollY.get() > 50) {
        scrollAnimationSvg.start({ fill: "white" });
      } else {
        scrollAnimationSvg.start({ fill: "black" });
      }
    });
  }, [scrollY]);
  const onValid = (data: IForm) => {
    history.push(`/search?keyword=${data.keyword}`);
  };

  return (
    <Nav animate={scrollAnimation}>
      <Col>
        <Logo
          variants={logoVariants}
          whileHover="active"
          initial="inActive"
          xmlns="http://www.w3.org/2000/svg"
          width="1024"
          height="276.742"
          viewBox="0 0 1024 276.742"
        >
          <path d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z" />
          <motion.path d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z" />
        </Logo>
        <Items>
          <Item>
            <NavLink
              style={{ textDecoration: "none", color: "black" }}
              to={"/"}
            >
              <motion.span animate={scrollAnimationMenuText}>Home</motion.span>{" "}
              {homeMatch?.isExact && <Circle layoutId="circle" />}
            </NavLink>
          </Item>
          <Item>
            <NavLink
              style={{ textDecoration: "none", color: "black" }}
              to={"/tv"}
            >
              <motion.span animate={scrollAnimationMenuText}>Tv</motion.span>
              {tvMatch?.isExact && <Circle layoutId="circle" />}
            </NavLink>
          </Item>
        </Items>
      </Col>
      <Col>
        <Search onSubmit={handleSubmit(onValid as any)}>
          <Input
            {...register("keyword", { required: true, minLength: 2 })}
            type="text"
            placeholder="Search"
            transition={{ duration: 0.2, type: "linear" }}
            animate={{ scale: searchOpen ? 1 : 0 }}
          />
          <motion.svg
            onClick={onClickSearch}
            style={{ scale: 0.7 }}
            animate={scrollAnimationSvg}
            transition={{ duration: 0.2, type: "linear" }}
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            ></path>
          </motion.svg>
        </Search>
      </Col>
    </Nav>
  );
}

export default Header;
